declare var jQuery:any;
declare var $: any;

export class Template {
    static initReadOnly() {
        $(".ng-select-container").css({"background": "white", "border": "0",  "font-weight": "500", "color": "#686868", "font-size": "15px"});
        
    }
    static initPage() {
        $(".sidebartoggler").on('click', function () {
            if ($("body").hasClass("mini-sidebar")) {
                $("body").trigger("resize");
                $(".scroll-sidebar, .slimScrollDiv").css("overflow-x", "hidden").parent().css("overflow", "visible");
                $("body").removeClass("mini-sidebar");
                $('.navbar-brand span').show();
                $('.navbar-brand b img').addClass("d-none d-xl-none");
                //$(".sidebartoggler i").addClass("ti-menu");
            }
            else {
                $("body").trigger("resize");
                $(".scroll-sidebar, .slimScrollDiv").css("overflow-x", "visible").parent().css("overflow", "visible");
                $("body").addClass("mini-sidebar");
                $('.navbar-brand span').hide();
                $('.navbar-brand b img').removeClass("d-none d-xl-none");
                //$(".sidebartoggler i").removeClass("ti-menu");
            }
        });

        $('#sidebarnav').metisMenu();
    }

    static initFilter() {        
        jQuery(document).on('click', '.columnsFilterDropDownMenu', function (e) {
            e.stopPropagation()
        });
        jQuery(document).on('click', '#filter-close', function (e) {
            //e.preventDefault()
        });
    }

    static initLayout() {
        $(".preloader").fadeOut();

        jQuery(document).on('click', '.mega-dropdown', function (e) {
            e.stopPropagation()
        });

        function set() {
            var width = (window.innerWidth > 0) ? window.innerWidth : this.screen.width;
            var topOffset = 70;
            if (width < 1170) {
                $("body").addClass("mini-sidebar");
                $('.navbar-brand span').hide();
                $(".scroll-sidebar, .slimScrollDiv").css("overflow-x", "visible").parent().css("overflow", "visible");
                $(".sidebartoggler i").addClass("ti-menu");
                $('.navbar-brand b img').removeClass("d-none d-xl-none");
            }
            else {
                $("body").removeClass("mini-sidebar");
                $('.navbar-brand span').show();
                $('.navbar-brand b img').addClass("d-none d-xl-none");
                //$(".sidebartoggler i").removeClass("ti-menu");
            }
            
            var height = ((window.innerHeight > 0) ? window.innerHeight : this.screen.height) - 1;
            height = height - topOffset;
            if (height < 1) height = 1;
            if (height > topOffset) {
                $(".page-wrapper").css("min-height", (height) + "px");
            }
        }

        $(window).ready(set);
        $(window).on("resize", set);

        $(".fix-header .topbar").stick_in_parent({});

        $('.floating-labels .form-control').on('focus blur', function (e) {
            $(this).parents('.form-group').toggleClass('focused', (e.type === 'focus' || this.value.length > 0));
        }).trigger('blur');

        $(".nav-toggler").click(function () {
            $("body").toggleClass("show-sidebar");
            $(".nav-toggler i").toggleClass("ti-menu");
            $(".nav-toggler i").addClass("ti-close");
        });
        $(".page-wrapper").click(function () {
            if (document.querySelector('.show-sidebar')){
                $("body").toggleClass("show-sidebar");
                $(".nav-toggler i").toggleClass("ti-menu");
                $(".nav-toggler i").addClass("ti-close");
            } else {
                // Do something if class does not exist
            }
            
        });
        

        $(".search-box a, .search-box .app-search .srh-btn").on('click', function () {
            $(".app-search").toggle(200);
        });

        // ============================================================== 
        // Right sidebar options
        // ============================================================== 
        $(".right-side-toggle").click(function () {
            $(".right-sidebar").slideDown(50);
            $(".right-sidebar").toggleClass("shw-rside");
        });

        $('[data-toggle="tooltip"]').tooltip();

        $('[data-toggle="popover"]').popover();

        $('.message-center').slimScroll({
            position: 'right'
            , size: "5px"
            , color: '#dcdcdc'
        });

        $('.aboutscroll').slimScroll({
            position: 'right'
            , size: "5px"
            , height: '80'
            , color: '#dcdcdc'
        });

        $('.message-scroll').slimScroll({
            position: 'right'
            , size: "5px"
            , height: '570'
            , color: '#dcdcdc'
        });

        $('.chat-box').slimScroll({
            position: 'right'
            , size: "5px"
            , height: '470'
            , color: '#dcdcdc'
        });

        $('.slimscrollright').slimScroll({
            height: '100%'
            , position: 'right'
            , size: "5px"
            , color: '#dcdcdc'
        });

        $("body").trigger("resize");

        $(".list-task li label").click(function () {
            $(this).toggleClass("task-done");
        });

        $('#to-recover').on("click", function () {
            $("#loginform").slideUp();
            $("#recoverform").fadeIn();
        });

        // ============================================================== 
        // Collapsable cards
        // ==============================================================
        $('a[data-action="collapse"]').on('click', function (e) {
            e.preventDefault();
            $(this).closest('.card').find('[data-action="collapse"] i').toggleClass('ti-minus ti-plus');
            $(this).closest('.card').children('.card-body').collapse('toggle');

        });
        // Toggle fullscreen
        $('a[data-action="expand"]').on('click', function (e) {
            e.preventDefault();
            $(this).closest('.card').find('[data-action="expand"] i').toggleClass('mdi-arrow-expand mdi-arrow-compress');
            $(this).closest('.card').toggleClass('card-fullscreen');
        });

        // Close Card
        $('a[data-action="close"]').on('click', function () {
            $(this).closest('.card').removeClass().slideUp('fast');
        });

        // ============================================================== 
        // This is for the sparkline charts which is coming in the bradcrumb section
        // ==============================================================
        $('#monthchart').sparkline([5, 6, 2, 9, 4, 7, 10, 12], {
            type: 'bar',
            height: '35',
            barWidth: '4',
            resize: true,
            barSpacing: '4',
            barColor: '#1e88e5'
        });
        $('#lastmonthchart').sparkline([5, 6, 2, 9, 4, 7, 10, 12], {
            type: 'bar',
            height: '35',
            barWidth: '4',
            resize: true,
            barSpacing: '4',
            barColor: '#7460ee'
        });
    }
}